<template>
  <form class="font-size_12 subscribtionForm parent row nowrap grow h-between margin-top_base-phone margin-bottom_base-phone">
    <label 
      class="subscribtionForm__label parent centered margin-left_auto width_fill-phone"
      for="subscribtion_email">
      –ü–æ–¥–ø–∏—Å–∞—Ç—å—Å—è –Ω–∞ —Ä–∞—Å—Å—ã–ª–∫—É:
    </label>
    <div class="relative controller parent row nowrap grow margin_centered-phone margin-top_small-phone">
      <!-- <p>–í—ã –≤–≤–µ–ª–∏: {{ subscribtion_email }}</p> -->
      <input 
        id="subscribtion_email"
        v-model.trim="subscribtion_email"
        class="round_25 controller__input"
        type="email" 
        name="subscribtion_email" 
        placeholder="–í–≤–µ–¥–∏—Ç–µ –≤–∞—à e-mail" 
        autocomplete="email" 
      >
      <base-button 
        :action="onSubmit"
        type="submit" 
        modifier="blue"
        class-name="text_upper absolute position_right-base controller__button"
      >
        –û—Ç–ø—Ä–∞–≤–∏—Ç—å
      </base-button>		
    </div>
  </form>
</template>

<script>
import { requestUrls } from "@/constants/conf";
import { prevent, timeout } from "@/constants/pureFunctions";

export default {
  name: "SubscribtionForm",
  data: () => ({
    subscribtion_email: "",
    response: {
      serverMessage: "",
      success: false,
      error: false,
      requestion: false
    }
  }),
  methods: {
    setResponseData(data) {
      this.$set(this, "response", data);
    },
    onSubmit(event) {
      // –°—Å—ã–ª–∫–∞ –Ω–∞ –æ–±—ä–µ–∫—Ç —Å–æ—Å—Ç–æ—è–Ω–∏—è —Ñ–æ—Ä–º—ã.
      const oldResponseData = this.response;
      // –ò–∑–≤–ª–µ–∫–∞–µ–º –¥–∞–Ω–Ω—ã–µ. –ü—Ä–∏ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç–∏,
      // –∫–ª—é—á (user_email) –ø–æ –∫–æ—Ç–æ—Ä–æ–º—É –±—É–¥–µ—Ç –ø—Ä–∏—Å–≤–∞–∏–≤–∞—Ç—å—Å—è
      // –ø–æ–¥–ø–∏—Å–∫–∞ –º–æ–∂–Ω–æ –ø–æ–º–µ–Ω—è—Ç—å.
      const data = {
        user_email: this.subscribtion_email
      };

      // POST –∑–∞–ø—Ä–æ—Å –Ω–∞ –ø–æ–¥–ø–∏—Å–∫—É.
      axios({
        method: "post",
        url: requestUrls.subscribtion,
        // –ó–∞–±–æ—Ç–∏–º—Å—è –æ–± –±–µ–∑–æ–ø–∞—Å–Ω–æ—Å—Ç–∏.
        headers: {
          "X-CSRFToken": this.$store.state.csrftoken
        },
        data
      })
        .then(response => {
          // –£—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª—è–µ–º —Å–æ–æ–±—â–µ–Ω–∏–µ —Å —Å–µ—Ä–≤–µ—Ä–∞ –≤ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ.
          this.setResponseData({
            ...oldResponseData,
            serverMessage: response.data,
            requesting: false,
            success: true
          });
        })
        .catch(error => {
          // –°–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ.
          this.setResponseData({
            ...oldResponseData,
            serverMessage: `–í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è –æ—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞: ${error}`,
            requesting: false,
            error: true
          });
          // –¢–∞–π–º–∞—É—Ç, –ø–æ –∏—Å—Ç–µ—á–µ–Ω–∏—é –∫–æ—Ç–æ—Ä–æ–≥–æ —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–∏
          // —Å–∫—Ä—ã–≤–∞–µ—Ç—Å—è.
          timeout(() => {
            this.setResponseData({
              ...oldResponseData,
              error: false,
              serverMessage: ""
            });
          }, 3000);
        });
      prevent(event);
    }
  }
};
</script>

<style lang="sass" scoped>
@import '../assets/sass/conf/_colors.sass'
@import '../assets/sass/conf/_easing.sass'
@import '../assets/sass/conf/_sizes.sass'
@import '../assets/sass/conf/_breakpoints.sass'

.subscribtionForm
  max-width: 39%
  min-height: em(39, 12)
  
  @include breakpoint(max-width em($containerWidth))
    min-width: 100%
  @include breakpoint($phone-less)
      flex-wrap: wrap
  &__label
    padding-right: em(20, 12)
    @include breakpoint($phone-less)
        padding-right:  0
.controller
  max-width: em(265, 12)
  min-height: em(39, 12)
  &__input
    min-width: 100%
    text-indent: 14px
  &__button
    top: 3px
    right: 3px
    position: absolute
</style>



